import React, { Component } from 'react';
import { Card, CardImg, CardText, CardBody, CardTitle, Breadcrumb, BreadcrumbItem, Button, Modal, ModalBody, ModalHeader, Label } from 'reactstrap';
import { Link } from 'react-router-dom';
import { Control, LocalForm, Errors } from 'react-redux-form';
import { Loading } from './LoadingComponent';
import { baseUrl } from '../shared/baseUrl';
import { FadeTransform, Fade, Stagger } from 'react-animation-components'

const maxLength = (len) => (val) => !val || (val.length <= len);
const minLength = (len) => (val) => val && (val.length > len);

class CommentForm extends Component {

    constructor(props) {
        super(props);
        this.state = {
            isModalOpen: false
        }
        this.toggleModal = this.toggleModal.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    toggleModal() {
        this.setState({
            isModalOpen: !this.state.isModalOpen
        })
    }

    handleSubmit(values) {
        this.toggleModal();
        this.props.postComment(this.props.dishId, values.rating, values.author, values.comment)
    }

    render() {
        return (
            <div>
                <Button outline color="secondary" onClick={this.toggleModal}><span className="fa fa-pencil"> Submit Comment</span></Button>
                <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                    <ModalHeader>
                        Submit Comment
                    </ModalHeader>
                    <ModalBody>
                        <LocalForm onSubmit={this.handleSubmit}>
                            <Label htmlFor="rating">Rating</Label>
                            <Control.select model=".rating" name="rating" id="rating" className="form-control">
                                <option value="1">1</option>
                                <option value="2">2</option>
                                <option value="3">3</option>
                                <option value="4">4</option>
                                <option value="5">5</option>
                            </Control.select>

                            <Label htmlFor="author">Your Name</Label>
                            <Control.text model=".author" name="author" id="author" className="form-control" placeholder="Your Name"
                                validators={{
                                    maxLength: maxLength(15), minLength: minLength(2)
                                }}
                            />
                            <Errors
                                className="text-danger"
                                model=".author"
                                show="touched"
                                messages={{
                                    minLength: 'Must be greater than 2 characters',
                                    maxLength: 'Must be 15 characters or less'
                                }}
                            />
                            <Label for="comment">Comment</Label>
                            <Control.textarea model=".comment" name="comment" rows="6" id="comment" className="form-control mb-3"/>
                            <Button type="submit" color="primary">Submit</Button>
                        </LocalForm>
                    </ModalBody>
                </Modal>
            </div>
        )
    }
}

function RenderDishDetail({ dish }) {
    if (dish != null) {
        return (
            <FadeTransform in 
            transformProps={{
                exitTransform: 'scale(0.5) translateY(-50%)'
            }}>
                <Card >
                    <CardImg width="100%" src={dish.image} alt={dish.name} />
                    <CardBody>
                        <CardTitle>{dish.name}</CardTitle>
                        <CardText>{dish.description}</CardText>
                    </CardBody>
                </Card>
            </FadeTransform>
        );
    }
    else {
        return (
            <div></div>
        )
    }
}

function RenderComments({ comments, postComment, dishId }) {

    const comment = comments.map(comments => {
        return (
            <Fade in>          
            
                <div key={comments.id}>

                    <p>{comments.comment}</p>
                    <p>
                        -- {comments.author},  {(new Date(comments.date)).toLocaleDateString("en-us", { year: "numeric", month: "short", day: "2-digit" })}
                    </p>
                </div>

            </Fade>
        );
    });

    return (
        <>  
        <Stagger in>
            {comment}
        </Stagger>
            <CommentForm dishId={dishId} postComment={postComment}/>
        </>
    )
}

function DishDetail(props) {

    if(props.isLoading){
        
        return (
            <div className="container">
                <div className="row">
                    <Loading/>
                </div>
            </div>
        )
    } else if (props.errMess) {
        return (
            <div className="container">
                <div className="row">
                    <h4>{props.errMess}</h4>
                </div>
            </div>
        )
    }
   else if(props.dish != null)
    return (
        <div className="container">
            <div className="row">
                <Breadcrumb>
                    <BreadcrumbItem><Link to="/menu">Menu</Link></BreadcrumbItem>
                    <BreadcrumbItem active> {props.dish.name}</BreadcrumbItem>
                </Breadcrumb>
                <div className="col-12">
                    <h3>{props.dish.name}</h3>
                    <hr />
                </div>
                <div className="col-12 col-md-5 mt-3 mb-3" >
                    <RenderDishDetail dish={props.dish} 
                     postComment={props.postComment}
                     dishId={props.dish.id}
                    />
                </div>
                <div className="col-12 col-md-5 mt-3 mb-5" >
                    <h3>Comments</h3>
                    <RenderComments comments={props.comments} 
                    postComment={props.postComment}
                    dishId={props.dish.id}/>
                </div>
            </div>
        </div>
    );
    else 
        return(
            <div></div>
        )
}

export default DishDetail;